
.App{
    text-align: center;
    font-size: 1em;
}
.resultsTable{
    margin-left: auto;
    margin-right: auto;    
}
.resultsTable td{
    text-align: left;
    padding-bottom: 0.3em;
    width: 7vw;
}
.resultsTable td:nth-of-type(5n)
{
    width: 25vw;
}
.resultsTable td:nth-of-type(6n)
{
    text-transform: capitalize;
}
.resultsTable td:nth-of-type(3n)
{
    width: 10vw;
}
form input{
    display: inline-block;
    margin-right: 20px;
}
#labelsRow{
    font-weight: 700;
    text-transform: uppercase;
}
